import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import MySnippets from '../views/MySnippets.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/my-snippets',
    name: 'MySnippets',
    component: MySnippets
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
